@use '../../css/variables.module.scss' as var;

div .wave_section {
  width: 100%;
  height: 100%;
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  & canvas {
    position: absolute;
    width: 100%;
    height: 100%;
  }
  & .button_group {
    position: absolute;
    top: 1%;
    right: 1%;
    background: rgba(0, 0, 0, 0.05);
    padding: 1rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    gap: 0.75rem;

    &.close {
      background: none;
      padding: 0;
    }

    & .play_button {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0.5rem;
      margin-bottom: 0.5rem;
      background-color: var.$color-gray;
      color: #ffffff;
      font-size: 2.5rem;
      border-radius: 2.5rem;

      &:hover {
        background-color: adjust-color($color: var.$color-gray, $blackness: 5%);
      }
      &:active {
        background-color: adjust-color(
          $color: var.$color-gray,
          $blackness: 10%
        );
      }
    }

    & .animation_option {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}
